<template>
    <div id="account">
      <Header2  :FlagSignin=false :FlagAcc=true :FlagEdit=false :FlagAdded=false />
      <div class="content">
        <div class="con">
            <div class="con1">
                <div class="con1-1">头像</div>
                <div class="con1-2" v-if="this.$store.state.img"><img :src="'//elm.cangdu.org/img/'+this.$store.state.img" /></div>
                <div class="con1-2" v-else><img src="../style/img/2.jpg" /></div>
                <div class="con1-3"><van-icon name="arrow" /></div>
            </div>
            <input type="file" @change="gettou()" ref="inp" class="inp" />

           

            <div class="con2">
                <div class="con2-1">用户名</div>
                <div class="con2-2">{{this.$store.state.val.username}}</div>
                <div class="con2-3"><van-icon name="arrow" /></div>
            </div>
            <div class="con2" @click="Add()">
                <div class="con2-1">收货地址</div>
                <div class="con2-2"></div>
                <div class="con2-3"><van-icon name="arrow" /></div>
            </div>
        </div>

        <p class="c">账号绑定</p>
        <div class="cont">
          <div class="cont1">
                <div class="cont1-1">手机</div>
                <div class="cont1-2"></div>
                <div class="cont1-3"><van-icon name="arrow" /></div>
          </div>
        </div>

        <p class="c">安全设置</p>
        <div class="contt">
          <div class="con2">
                <div class="con2-1">登录密码</div>
                <div class="con2-2"></div>
                <div class="con2-3"><van-icon name="arrow" /></div>
          </div>
        </div>

        <button class="btn" @click="del()">退出登陆</button>

      </div>
    </div>
</template>

<script>
import Header2 from '../components/Header2' 
import Vue from 'vue';
import { Dialog } from 'vant';
export default {
  components:{
        Header2,[Dialog.Component.name]: Dialog.Component
    },
  name: 'Order',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      img:'',
      id:''
    }
  },
  created(){
     
  },
  methods:{
      del(){
        this.axios.get('http://elm.cangdu.org/v2/signout').then(res=>{
            Dialog.confirm({
              title: '标题',
              message: '弹窗内容'
            }).then(() => {
              // on confirm
               this.$store.commit('dell',res.data)
               this.$router.push('/Homepage')
            }).catch(() => {
              // on cancel
              
            });
        })
       
      },
      Add(){
        this.$router.push('./Edit')
      },
      gettou(){
          //获取input值
          let input = this.$refs.inp;
          console.log(input.files[0])
          //存储
          let data = new FormData();
          data.append("file",input.files[0]);//类型   值
          //从新设置请求头
          let headers={headers:{"Content-Type": "multipart/form-data"}};
          this.axios.post("http://elm.cangdu.org/eus/v1/users/38184/avatar",data,headers).then(res=>{
            console.log(res)
            if(res.data.image_path != ""){
              this.$store.commit('gettou',res.data.image_path);
              this.img=res.data.image_path
            }  
          })
      }
  },
  mounted(){
     
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#account{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F5F9;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        position: relative;
        margin-top: 2rem;
        .con{
            margin-top:0.4rem;
            background:white;
            .con1{
                display:flex;
                width: 100%;
                line-height:3rem;
                position: relative;
                border-bottom:0.01rem solid gainsboro;
                .con1-1{
                  margin-left:5%;
                }
                .con1-2{
                  position: absolute;
                  top:0.5rem;
                  right:2rem;
                    img{
                        width:2rem;
                        height:2rem;
                        border-radius:50%;
                    }
                }
                .con1-3{
                  position: absolute;
                  top:0.1rem;
                  right:1rem;
                }
            }
            .inp{
              width: 80%;
              height: 3rem;
              position: fixed;
              top:2rem;
              opacity: 0;
            }
            .con2{
                display:flex;
                width: 100%;
                line-height:2rem;
                border-bottom:0.01rem solid gainsboro;
                position: relative;
                .con2-1{
                  margin-left:5%;
                }
                .con2-2{
                  position: absolute;
                  // top:0.2rem;
                  right:2rem;
                }
                .con2-3{
                  position: absolute;
                  top:0.1rem;
                  right:1rem;
                }
            }
            .con2:nth-of-type(1){
              border:none;
            }
        }
        .c{
          width:100%;
          line-height:1.5rem;
          margin-left:5%;
        }
        .cont{
         
            background:white;
            .cont1{
                display:flex;
                width: 100%;
                line-height:2rem;
                 position: relative;
                .cont1-1{
                  margin-left:5%;
                }
                .cont1-2{
                  position: absolute;
                  // top:0.2rem;
                  right:2rem;
                }
                .cont1-3{
                  position: absolute;
                  top:0.1rem;
                  right:1rem;
                }
            }
        }
        .contt{
          background:white;
         
            .con2{
                display:flex;
                width: 100%;
                line-height:2rem;
                 position: relative;
                 .con2-1{
                  margin-left:5%;
                }
                 .con2-2{
                  position: absolute;
                  // top:0.2rem;
                  right:2rem;
                }
                .con2-3{
                  position: absolute;
                  top:0.1rem;
                  right:1rem;
                }
            }
        }
        .btn{
          width:90%;
          margin-left:5%;
          background:#d8584a;
          border:none;
          margin-top:1rem;
          color:white;
          line-height:1.5rem;
          border-radius:5px;
        }
    }
    
}
    
</style>



